.full-stack-craft-button {
  line-height: 36px !important;
  height: 37px !important;
  text-decoration: none !important;
  display: inline-block !important;
  color: #ffffff !important;
  background-color: #000966 !important;
  border-radius: 3px !important;
  border: 1px solid transparent !important;
  padding: 1px 9px !important;
  font-size: 23px !important;
  letter-spacing: 0.6px !important;
  box-shadow: 0px 1px 2px rgba(190, 190, 190, 0.5) !important;
  -webkit-box-shadow: 0px 1px 2px 2px rgba(190, 190, 190, 0.5) !important;
  margin: 0 auto !important;
  font-family: 'Fira Code', cursive !important;
  box-sizing: border-box !important;
  transition: 0.3s all linear !important; }
  .full-stack-craft-button:hover, .full-stack-craft-button:active, .full-stack-craft-button:focus {
    -webkit-box-shadow: 0px 1px 2px 2px rgba(190, 190, 190, 0.5) !important;
    text-decoration: none !important;
    box-shadow: 0px 1px 2px 2px rgba(190, 190, 190, 0.5) !important;
    opacity: 0.85 !important;
    color: #000966 !important;
    background-color: #01a2ed !important; }
  .full-stack-craft-button img {
    width: 27px !important;
    margin-bottom: 3px !important;
    box-shadow: none !important;
    border: none !important;
    vertical-align: middle !important;
    border-radius: 100%; }
  .full-stack-craft-button span {
    margin-left: 7px; }

body {
  font-family: courier new;
  margin: 0;
  padding: 0;
  background: black;
  color: white;
}
a {
  color: #808080;
  font-weight: bold;
}
a:visited {
  color: #696969;
}
h1 {
  text-align: center;
  margin-bottom: 0;
}
.container {
  max-width: 690px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.sampleInvaders {
  max-height: 25px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.schematic {
  max-width: 99%;
  min-width: 225px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* phones and tablets */
@media all and (max-width: 690px) { 
  h1,h2,h3,p {
    padding: 1rem;
  }
  .container {
    max-width: 100%;
  }
}

